import React from 'react';
import classes from './Travels.module.scss';
import TravelsItem from './TravelsItem/TravelsItem';
import noimg from '../../assets/images/noimg.png';
import { lang } from '../../Helpers/Helpers';

const travels = (props) => {

    const travelsList = props.data.slice(1).map((entry, index) => (
        <TravelsItem
            img={entry.imgPaths ? entry.imgPaths : noimg}
            title={JSON.parse(entry.title)[props.lang]}
            content={JSON.parse(entry.short_content)[props.lang]}
            innerContent={JSON.parse(entry.content)[props.lang]}
            link={`/${props.lang}/sayohatlar/${entry.id}`}
            key={index} />
    ));
    // "/" + props.lang + "/sayohatlar"+"/" + props.data.id
    const forMainTravel = props.data.slice(0, 1).map((entry, index) => (
        <TravelsItem
            img={entry.imgPaths ? entry.imgPaths : noimg}
            title={JSON.parse(entry.title)[props.lang]}
            content={JSON.parse(entry.short_content)[props.lang]}
            innerContent={JSON.parse(entry.content)[props.lang]}
            link={`/${props.lang}/sayohatlar/${entry.id}`}
            key={index} main />
    ));
    return (
        <section className={classes.travels}>
            <h2 className={classes.title}>{lang(props.lang, 'travels')}</h2>
            <div className="container">
                <div className={classes.wrapper}>
                    <div className={classes.mainTravel}>
                        {forMainTravel}
                    </div>
                    <div className={classes.otherTravels}>{travelsList}</div>
                </div>
            </div>
        </section>
    )
}

export default travels