import React from 'react';
import classes from './NewsItem.module.scss';
import { Link } from "react-router-dom"
import { faCalendarAlt } from '@fortawesome/free-solid-svg-icons'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import noimg from '../../../assets/images/noimg.png'
import { alt, lang } from '../../../Helpers/Helpers'
const calendar = <FontAwesomeIcon color="#000" icon={faCalendarAlt} />

const convertDigitIn = date => date.split("-").reverse().join('-')

const newsItem = (props) => {
    let date = '12-01-2020'
    if (props.date) {
        date = convertDigitIn(props.date)
    }

    const newTo = {
        pathname: "/" + props.lang + "/newsContent/" + props.id,
        content: props.content,
        title: props.title
    };
    // console.log(props.title);
    
    return (
        <div className={props.boxed ? "col-lg-3 col-sm-6" : null}>
            <div className={classes.newsItem}>
                <img src={props.src ? props.src : noimg} alt={alt(props.src)} />
                <Link to={newTo} className={classes.content}>{props.title}</Link>
                <div className={classes.cardBottom}>
                    <span className={classes.newsDate}> {calendar} &nbsp; {date}</span>
                    <Link to={newTo} className={classes.newsOne} >{lang(props.lang, 'read-more')} <i className="fa fa-arrow-right"></i></Link>
                </div>
            </div>
        </div>
    )
};

export default newsItem;