import React, { Component } from 'react';
import logo from '../../assets/images/LogoLoader.svg'
class FetchMultiple extends Component {
	state = {
		data: {},
		received: [],
		got: false
	}

	componentDidMount() {
		if (!this.props.flag) {
			this.props.urls.forEach(url => {
				fetch(url.url)
					.then(response => response.json())
					.then(json => {
						this.setState({
							data: {
								...this.state.data,
								[url.for]: json
							},
							got: true
						})
						this.props.setData(this.state.data)
					})
					.catch(err => console.log(err))
			});
		} else {
			this.setState({ data: this.props.appState, got: true })
		}
	}
	render() {

		const C = this.props.C;
		return (
			<div>
				{Object.keys(this.state.data).length === this.props.urls.length ?
					<C data={this.state.data} {...this.props} /> :
					<div className="loader">
						<img src={logo} alt="logo"/>
					</div>
				}
			</div>
		);
	}
};
export default FetchMultiple;