import React, { Component } from 'react';
import NavLinks from '../../components/NavLinks/NavLinks'
import Backdrop from '../../components/UI/Backdrop/Backdrop';
import Logo from '../../components/UI/Logo/Logo';
import SocialItems from '../../components/UI/SocialItems/SocialItems'
import classes from './Header.module.scss';
import { Link } from "react-router-dom"

class Header extends Component {
    state = {
        showSideDrawer: false,
        langOpen: false,
        langLinks: {
            ru: "/ru",
            oz: "/oz",
            en: "/en",
            uz: "/uz",
        }
    }

    navLinksOpener = () => {
        this.setState({ showSideDrawer: true })
    }

    navLinksCloser = () => {
        this.setState({ showSideDrawer: false })
    }

    langToggle = (e) => {
        if (this.state.langOpen) {
            this.setState({ langOpen: false })
        } else {
            this.setState({ langOpen: true })
        }

    }
    render() {
        const path = this.props.location.pathname.slice(3)
        const ruLink = "/ru" + path
        const uzLink = "/uz" + path
        const enLink = "/en" + path
        const ozLink = "/oz" + path

        return (
            <header className={classes.header}>
                <div className="container">
                    <div className={classes.headerItems}>
                        <div className={classes.headerRight}>
                            <div>
                                <div className={classes.burger} onClick={this.navLinksOpener}>
                                    <div className="line1"></div>
                                    <div className="line2"></div>
                                    <div className="line3"></div>
                                </div>
                                <nav><NavLinks open={this.state.showSideDrawer} close={this.navLinksCloser} logo header lang={this.props.match.params.lang} /></nav>
                            </div>
                            <SocialItems />
                            <div className={classes.langChanger}>
                                <div onClick={this.langToggle}>
                                    {this.props.match.params.lang === "uz" ? "" :
                                        (this.props.match.params.lang === "oz" ? "uz" :
                                            this.props.match.params.lang)}
                                </div>
                                {this.state.langOpen && <ul>
                                    <li>
                                        <Link to={ruLink} onClick={this.langToggle}>
                                            ru
                                        </Link>
                                    </li>
                                    <li>
                                        <Link to={uzLink} onClick={this.langToggle}>
                                            
                                        </Link>
                                    </li>
                                    <li>
                                        <Link to={enLink} onClick={this.langToggle}>
                                            en
                                        </Link>
                                    </li>
                                    <li>
                                        <Link to={ozLink} onClick={this.langToggle}>
                                            uz
                                        </Link>
                                    </li>
                                </ul>}
                            </div>
                        </div>
                        <div className={classes.logo}>
                            <Logo lang={this.props.match.params.lang} />
                        </div>
                    </div>
                </div>
                <Backdrop show={this.state.showSideDrawer} clicked={this.navLinksCloser} />
            </header>
        )
    }

}

export default Header;