import { ru } from './lang/ru'
import { oz } from './lang/oz'
import { uz } from './lang/uz'
import { en } from './lang/en'

let langForOthers = ''
const lang = (lang, content) => {
    langForOthers = lang;
    switch (lang) {
        case 'ru':
            return ru(content)
        case 'oz':
            return oz(content)
        case 'uz':
            return uz(content)
        case 'en':
            return en(content)
        default:
            break;
    }
}


const alt = (param) => {
    if (typeof (param) === 'object' && param) {
        return param.map(entry => entry.replace(/^.*[\\/]/, ''));
    } else if (param) {
        return param.replace(/^.*[\\/]/, '');
    } else {
        return 'noimg'
    }
}


export { lang, alt, langForOthers }