import React from 'react';
import { WithData } from "../../../HOCs"
import AllNews from "./AllNews"
import AppContext from "../../../Context/AppContext"
import { baseUrl } from "../../../Constants"

const AllNewsWrapper = (props) => {
	return (
		<AppContext.Consumer>
			{(context) => {
				return <WithData C={AllNews}{...props}
					url={baseUrl + "/api/news"}
					setData={context.setYangiliklar}
					appState={context.yangiliklar}
					flag={context.yangiliklarGot}
					{...props}
				/>
			}
			}
		</AppContext.Consumer>
	)
}

export default AllNewsWrapper;