import React, { Component } from 'react';
import classes from "./Dasturlar.module.css"
import { TopPageHeader, ListBlock } from "../../components"
import background from '../../assets/images/header-parallax.jpg'


class Dasturlar extends Component {
	componentDidMount() {
		window.scrollTo(0, 0);
	}

	render() {
		// console.log(this.props.content);

		return (
			<div className={classes.root}>
				<TopPageHeader title="Dasturlar" background={background} />
				<div className="container">
					<div className={classes.padded}>
						<ListBlock page="programs" data={this.props.data.data} lang={this.props.match.params.lang} programs />
					</div>
				</div>
			</div>

		);
	}
}
export default Dasturlar