import React, { Component } from 'react';
import classes from './GalleryItem.module.scss';
import like from '../../../assets/images/icons/like.svg';
import photoIcon from '../../../assets/images/icons/photo.svg'
import { Link } from "react-router-dom"
import noimg from '../../../assets/images/noimg.png'
import { baseUrl } from '../../../Constants/index'

const axios = require('axios');

class galleryItem extends Component {

    state = {
        likes: this.props.flag ? this.props.appState[this.props.title] : this.props.likes,
        id: this.props.id
    }

    likesHandler = () => {

        axios.get(`${baseUrl}/api/likedPost`, {
            params: {
                p_id: this.state.id
            }
        })
            .then((response) => {
                if (response.data.success) {
                    this.setState((state) => ({ likes: (parseInt(state.likes) + 1).toString() }))
                }
            })
            .then(() => {
                // console.log(this.state.likes);
                this.props.setLikes(this.props.title, this.state.likes)
            })
            .catch(function (error) {
                console.log(error);
            })
    }
    componentDidMount() {
        if (!this.props.flag) {
            this.props.setLikes(this.props.title, this.props.likes)
        }
    }
    render() {
        const newTo = {
            pathname: '/' + this.props.lang + "/gallery/" + this.props.id,
            imgs: this.props.img,
            title: this.props.title
        };

        return (
            <div className="col-lg-3 col-md-4 col-sm-6">
                <div className={classes.galleryItem}>
                    <Link to={newTo} className={classes.imgs}>
                        <img src={photoIcon} alt="icon" className={classes.photoIcon} />
                        <img src={this.props.img ? this.props.img[0] : noimg} alt={this.props.title} className={classes.mainImg} />
                    </Link>
                    <div className={classes.content}>
                        <h5>{this.props.title}</h5>
                        <div>
                            <span>{this.props.date}</span>
                            <button onClick={this.likesHandler} type="button" className={classes.like}>
                                <img src={like} alt='like' />
                                <span>{this.props.appState[this.props.title]}</span>
                            </button>
                            {/* <span onClick={this.likesHandler}><img src={like} alt='like' />
                                {this.props.appState[this.props.title]}
                            </span> */}
                        </div>
                    </div>
                </div>
            </div>
        )
    }

};

export default galleryItem


