import "./App.css"
import React, { Component } from "react";
import {
  IndexWrapper, DasturlarWrapper, GalleryBuilderWrapper,
  Sayohatlar, DasturWrapper, ProgramsContent,
  SayohatlarWrapper, Teleshows
} from '../containers';
import NewsContentWrapper from '../containers/News/NewsContent/NewsContentWrapper'
import AllNewsWrapper from "../containers/News/AllNews/AllNewsWrapper"
import AppContext from "../Context/AppContext"
import { BrowserRouter, Route, Switch, Redirect } from "react-router-dom";
import Footer from '../components/Footer/Footer'
import Header from '../containers/Header/Header'
import Contact from "../components/Contact/Contact";
import GalleryViewWrapper from "../containers/GalleryView/GalleryViewWrapper";


class App extends Component {

  state = {
    yangiliklar: [],
    yangiliklarGot: false,
    dasturlar: [],
    dasturlarGot: false,
    sayohatlar: [],
    sayohatlarGot: false,
    gallery: [],
    galleryGot: false,
    likes: {},
    likesGot: false,
    indexData: {},
    indexGot: false
  }
  setYangiliklar = (news) => {
    // console.log(news);
    this.setState({ yangiliklar: news, yangiliklarGot: true })
  }
  setDasturlar = (programs) => {
    // console.log(programs);
    this.setState({ dasturlar: programs, dasturlarGot: true })
  }
  setSayohatlar = (trips) => {
    // console.log(trips);
    this.setState({ sayohatlar: trips, sayohatlarGot: true })
  }

  setGallery = (gallery) => {
    this.setState({ gallery: gallery, galleryGot: true })
  }
  
  setLikes = (title, likes) => {
    const data = {
      [title]: likes
    }
    this.setState((state) => ({ likes: { ...state.likes, ...data }, likesGot: true }))
  }
  
  setIndex = (index) => {
    this.setState({ indexData: index, indexGot: true })
  }

  render() {
    
    return (
      <AppContext.Provider
        value={
          {
            yangiliklar: this.state.yangiliklar,
            yangiliklarGot: this.state.yangiliklarGot,
            setYangiliklar: this.setYangiliklar,
            dasturlar: this.state.dasturlar,
            dasturlarGot: this.state.dasturlarGot,
            setDasturlar: this.setDasturlar,
            sayohatlar: this.state.sayohatlar,
            sayohatlarGot: this.state.sayohatlarGot,
            setSayohatlar: this.setSayohatlar,
            gallery: this.state.gallery,
            galleryGot: this.state.galleryGot,
            setGallery: this.setGallery,
            likes: this.state.likes,
            likesGot: this.state.likesGot,
            setLikes: this.setLikes,
            index: this.state.indexData,
            indexGot: this.state.indexGot,
            setIndex: this.setIndex
          }
        }>
        <BrowserRouter>
          <Route component={Header} path="/:lang(uz|oz|ru|en)" />
          <Switch>
            <Route component={IndexWrapper} path="/:lang(uz|oz|ru|en)" exact />
            <Route component={DasturlarWrapper} path="/:lang(uz|oz|ru|en)/programs" exact />
            <Route component={DasturWrapper} path="/:lang(uz|oz|ru|en)/programs/:id" exact />
            <Route component={Contact} path="/contact" />
            <Route component={GalleryBuilderWrapper} path="/:lang(uz|oz|ru|en)/gallery" exact />
            <Route component={GalleryViewWrapper} path="/:lang(uz|oz|ru|en)/gallery/:id" exact />
            <Route component={SayohatlarWrapper} path="/:lang(uz|oz|ru|en)/sayohatlar" exact />
            <Route component={ProgramsContent} path="/:lang(uz|oz|ru|en)/sayohatlar/:id" exact />
            <Route component={Sayohatlar} path="/:lang(uz|oz|ru|en)/sayohatlar" exact />
            <Route component={Teleshows} path="/:lang(uz|oz|ru|en)/teleshow" exact />
            <Route component={ProgramsContent} path="/:lang(uz|oz|ru|en)/sayohat/:id" exact />
            <Route component={AllNewsWrapper} path="/:lang(uz|oz|ru|en)/allNews" exact />
            <Route component={Contact} path="/:lang(uz|oz|ru|en)/contact" exact />
            <Route component={NewsContentWrapper} path="/:lang(uz|oz|ru|en)/newsContent/:id" exact />
            <Redirect component={IndexWrapper} path="/" exact to="/oz" />
          </Switch>
          <Route component={Footer} path="/:lang(uz|oz|ru|en)" />
        </BrowserRouter>
      </AppContext.Provider>
    );
  }
}

export default App;

