import React, { Component } from "react";
import Lightbox from "react-image-lightbox";
import FullContent from '../../HOCs/FullCont/FullCont';
import { Helmet } from 'react-helmet'
import "react-image-lightbox/style.css";
import classes from './GalleryView.module.css';
import noimg from '../../assets/images/noimg.png'

class GalleryView extends Component {

    constructor(props) {
        super(props);

        this.state = {
            photoIndex: 0,
            isOpen: false,
            imgs: this.props.location.imgs ?
                this.props.location.imgs :
                (this.props.imgs ? this.props.imgs : [noimg]),
        };
    }

    componentDidMount() {
        window.scroll(0, 0)
    }


    render() {
        const list = this.state.imgs.map((entry, index) => {
            return (
                <div className='col-xl-3 col-lg-4 col-sm-6' key={index}>
                    <img src={entry} onClick={() => this.setState({ photoIndex: index, isOpen: true })} key={index} alt='' />
                </div>
            )
        });

        const { photoIndex, isOpen } = this.state;

        return (
            <>
                <Helmet>
                    <title>{this.props.title}</title>
                    <meta name="description" content={this.props.title} />
                </Helmet>
                
                <div className={classes.galleryItem}>
                    <h2>{this.props.title}</h2>
                    <FullContent>
                        {list}
                    </FullContent>
                </div>

                {isOpen && (
                    <Lightbox
                        mainSrc={this.state.imgs[photoIndex]}
                        nextSrc={this.state.imgs[(photoIndex + 1) % this.state.imgs.length]}
                        prevSrc={this.state.imgs[(photoIndex + this.state.imgs.length - 1) % this.state.imgs.length]}
                        onCloseRequest={() => this.setState({ isOpen: false })}
                        onMovePrevRequest={() =>
                            this.setState({
                                photoIndex: (photoIndex + this.state.imgs.length - 1) % this.state.imgs.length
                            })
                        }
                        onMoveNextRequest={() =>
                            this.setState({
                                photoIndex: (photoIndex + 1) % this.state.imgs.length
                            })
                        }
                    />
                )}
            </>
        );
    }
}

export default GalleryView;