import React from 'react';
import GalleryBuilder from "./GalleryBuilder"
import AppContext from "../../Context/AppContext"
import { WithData } from "../../HOCs"
import { baseUrl } from "../../Constants"
const IndexWrapper = (props) => {

	return (
		<AppContext.Consumer>
			{(context) => {
				return <WithData C={GalleryBuilder}{...props}
					url={baseUrl + "/api/gallery"}
					setData={context.setGallery}
					appState={context.gallery}
					flag={context.galleryGot}
					{...props}
				/>
			}
			}
		</AppContext.Consumer>
	)
}

export default IndexWrapper;