import React from 'react';
import { Map, YMaps, Placemark } from "react-yandex-maps";
import "./Contact.css";

const Contact = () => {
    return (
        <div className="contact-container">
            <div className="contact-header">
                Contact
            </div>

            <div className="contact">
                <div className="container">
                    <div className="row">
                        <div className="col-lg-8 col-12">
                            <div className="map-container">
                                <YMaps>
                                    <Map
                                        defaultState={{
                                            center: [41.321973, 69.242395],
                                            zoom: 13,
                                            controls: ['zoomControl', 'fullscreenControl']
                                        }}
                                        className="org-map"
                                        modules={['control.ZoomControl', 'control.FullscreenControl', "layout.ImageWithContent"]}>
                                        <Placemark
                                            defaultGeometry={[41.321973, 69.242395]}
                                            modules={["geoObject.addon.hint"]}
                                            options={{
                                                iconLayout: 'default#image',
                                                iconImageSize: [30, 40],
                                                iconImageOffset: [-3, -42]
                                            }}
                                            properties={{
                                                hintContent: 'Dunyo Boylab',
                                                balloonContent: '  '
                                            }} />
                                    </Map>
                                </YMaps>
                            </div>
                        </div>
                        <div className="col-lg-4 col-12">
                            <div className="contact-form">
                                <input
                                    type="text"
                                    className="contact-input"
                                    placeholder=": " />
                                <input
                                    type="email"
                                    className="contact-input"
                                    placeholder="Email: " />
                                <input
                                    type="text"
                                    className="contact-input"
                                    placeholder=": " />
                                <textarea rows="6" cols="45" className="message" placeholder=": "></textarea>
                                <button type="button" className="send-button">O</button>
                            </div>
                        </div>
                    </div>
                </div>
            </div>

        </div>
    );
};

export default Contact;



