import React from 'react'
import InnerContent from '../../../components/InnerContent/InnerContent'
import AppContext from "../../../Context/AppContext"

const newsContent = (props) => {
    return (

        <AppContext.Consumer>
            {(context) => {
                return <InnerContent
                    content={props.location.content ? props.location.content : props.content}
                    title={props.location.title ? props.location.title : props.title}
                    recentData={context.index.news}
                    id={props.id}
                    category='news' />
            }}
        </AppContext.Consumer>

    )
}

export default newsContent;