import React from 'react';
import classes from "./TopPageHeader.module.css"

const TopPageHeader = (props) => {
  return (
    	<div className={classes.root} style={{backgroundImage: `url(${props.background})`}}>
    		<div className={classes.overlay}></div>
    		<h1>{props.title}</h1>
    	</div>
    
  )
}

export default TopPageHeader;