import React from "react";
import Slider from "react-animated-slider";
import { Link } from "react-router-dom"
import "react-animated-slider/build/horizontal.css";
import "normalize.css/normalize.css";
import "./Slider.scss";
import classes from "./Slider.module.scss";

const slider = (props) => {

    return (
        <>
            <Slider className='slider-wrapper' touchDisabled={true} autoplay={2000}>
                {props.content.map((item, index) => {
                    // console.log(item.videoLink);
                    
                    const newTo = {
                        pathname: item.link ? item.link : '',
                        content: item.content ? item.content : '',
                        title: item.title ? item.title : '',
                        day: item.day ? item.day : '',
                        time: item.time ? item.time : '',
                        imgs: item.imgs ? item.imgs : null,
                        videoLink: item.videoLink ? item.videoLink : null
                    };
                    
                    return (
                        <div
                            key={index}
                            className="slider-content"
                            style={{ background: `url('${item.image}') no-repeat center center` }}>
                            <div className="container inner">
                                {item.title ?
                                    <div>
                                        {item.link ?
                                            <h3><Link to={newTo}>{item.title}</Link></h3> :
                                            <h3>{item.title}</h3>
                                        }
                                    </div> : null}
                                {item.description ? <h1 dangerouslySetInnerHTML={{ __html: item.description }} /> : null}
                                {props.noDate ? null :
                                    <div className={`date ${classes.date}`}>
                                        <span>{item.day}</span>
                                        {item.time}
                                    </div>
                                }
                                {/* {item.link ?
                                <div>
                                    <a style={props.linkStyle} href={item.link}></a>
                                </div>
                                : null} */}
                            </div>
                            {item.imgs ?
                                <div className={classes.imgs}>
                                    {item.imgs.slice(0, 2).map((img, index) => {
                                        return (
                                            <img src={img} alt={index} key={index} />
                                        )
                                    })}
                                </div>
                                : null}
                        </div>
                    )
                })}
            </Slider>

        </>
    );



}

export default slider;
