import React from "react";
import Gallery from '../../components/Gallery/Gallery'
import { MainSlider, Programs, Travels } from '../../components'
import { News } from "../"
import { Helmet } from 'react-helmet'
import { lang } from '../../Helpers/Helpers'

const IndexBuilder = (props) => {
	return (
		<>
			<Helmet>
				<title>{lang(props.match.params.lang, 'index-title')}</title>
				<meta name="description" content={lang(props.match.params.lang, 'index-desc')} />
			</Helmet>
			<MainSlider data={props.data.premiere} lang={props.match.params.lang} />
			<News data={props.data.news} lang={props.match.params.lang} />
			<Travels data={props.data.trips} lang={props.match.params.lang} />
			<Programs data={props.data.programs} lang={props.match.params.lang} />
			<Gallery data={props.data.gallery} lang={props.match.params.lang} />
		</>
	)
};

export default IndexBuilder;

