import React, { useState, useEffect } from 'react';
import logo from '../../assets/images/LogoLoader.svg'
const GetLost = (props) => {
  var [hasData, changeHasData] = useState(false);
  var [data, changeData] = useState({});

  useEffect(() => {
    if (props.location.content && props.location.title) {
      changeHasData(true)
    } else {
      fetch(props.url + props.match.params.id)
        .then((data) => data.json())
        .then((json) => {
          changeData(json)
          changeHasData(true)
        })
        .catch(err => {
          console.log(err)
        })
    }
  }, [props.match.params.id, props.location.content, props.location.title, props.url])


  return (
    <div>
      {hasData ? <props.C {...props} data={data} /> :
        <div className="loader">
          <img src={logo} alt="logo" />
        </div>
      }
    </div>
  )
}

export default GetLost;