const en = (content) => {
    switch (content) {
        case 'News':
            return 'News'
        case 'Allnews':
            return 'All news'
        case 'programs':
            return 'Programs'
        case 'travels':
            return "Traveling the World"
        case 'gallery':
            return "Gallery"
        case 'bio':
            return "Together with you around the world!"
        case 'address':
            return "69, A. Navoi Street, 100011, Tashkent"
        case 'rights':
            return "Dunyo Bo'ylap TV Channel All rights reserved. Copying of materials without confirmation is prohibited."
        case 'shows':
            return "Tv Shows"
        case 'weekly':
            return "Weekly TV Shows"
        case 'world':
            return "Around the World"
        case 'contact':
            return "Contact"
        case 'read-more':
            return "Read more"
        case 'index-title':
            return "Dunyo Bo'ylab | Official TV channel"
        case 'index-desc':
            return "The official website of Dunyo Bo'ylap TV channel under National Television and Radio Company of Uzbekistan"
        case 'recent-news':
            return "The latest news"
        case 'recent-travels':
            return "Recent trips"
        case 'allnews-desc':
            return "News from around the world"
        default:
            break;
    }
}

export { en }