import React, { Component } from 'react';
import { TopPageHeader } from '../../../components'
import classes from './AllNews.module.css'
import allnews from '../../../assets/images/allNews.jpg'
import NewsItem from "../NewsItem/NewsItem"
import ReactPaginate from 'react-paginate';
import { faChevronRight, faChevronLeft } from '@fortawesome/free-solid-svg-icons'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { Helmet } from 'react-helmet'
import { lang } from '../../../Helpers/Helpers'

class AllNews extends Component {
    constructor(props) {
        super(props);
        this.state = {
            offset: 0,
            data: [],
            perPage: 8,
            currentPage: 0,
            pageCount: null,
            lang: this.props.match.params.lang
        };
    }
    receivedData() {
        const data = this.props.data.data
        const slice = data.slice(this.state.offset, this.state.offset + this.state.perPage)
        const list = slice.map((entry, index) => {
            return (
                <NewsItem boxed
                    src={entry.imgPaths}
                    title={JSON.parse(entry.title)[this.props.match.params.lang]}
                    key={index}
                    date={entry.inserted_at}
                    id={entry.id}
                    lang={this.props.match.params.lang}
                    content={JSON.parse(entry.content)[this.props.match.params.lang]} />
            )
        });
        this.setState({
            pageCount: Math.ceil(data.length / this.state.perPage),
            list
        })
    }

    handlePageClick = (e) => {
        const selectedPage = e.selected;
        const offset = selectedPage * this.state.perPage;
        this.setState({
            currentPage: selectedPage,
            offset: offset
        }, () => {
            this.receivedData()
        });

    };

    langHandler = () => {
        if (this.props.match.params.lang !== this.state.lang) {
            this.componentDidMount()
            this.setState({ lang: this.props.match.params.lang })
        }
    }

    componentDidMount() {
        this.receivedData()
        window.scroll(0, 0)
    }

    componentDidUpdate() {
        this.langHandler()
    }

    render() {

        return (
            <>
                <Helmet>
                    <title>{lang(this.props.match.params.lang, 'News')}</title>
                    <meta name="description" content={lang(this.props.match.params.lang, 'allnews-desc')} />
                </Helmet>
                <section className={classes.allNews}>
                    <TopPageHeader title={lang(this.props.match.params.lang, 'News')} background={allnews} />
                    <div className={"container " + classes.cards}>
                        <div className="row">
                            {this.state.list}
                        </div>
                        {this.state.pageCount > 1 ?
                            <ReactPaginate
                                previousLabel={<FontAwesomeIcon color="#000" icon={faChevronLeft} />}
                                nextLabel={<FontAwesomeIcon color="#000" icon={faChevronRight} />}
                                breakLabel={"..."}
                                breakClassName={"break-me"}
                                pageCount={this.state.pageCount}
                                marginPagesDisplayed={1}
                                pageRangeDisplayed={1}
                                onPageChange={this.handlePageClick}
                                containerClassName={`pagination ${classes.pagination}`}
                                subContainerClassName={"pages pagination"}
                                activeClassName={"active"} /> : null
                        }

                    </div>
                </section>
            </>
        );
    }

};


export default AllNews;

