import React, { useState, useEffect } from 'react';
import GalleryView from "./GalleryView"
import { langForOthers } from '../../Helpers/Helpers'
import Loader from '../../components/Loader/Loader';
const GalleryViewWrapper = (props) => {
    var [hasData, changeHasData] = useState(false);
    var [imgs, changeImg] = useState(null);
    let [title, changeTitle] = useState();
    let [comingTitle] = useState(props.location.title);

    useEffect(() => {
        if (props.location.imgs && props.location.title) {
            changeHasData(true)
        } else {
            fetch("http://admin.dunyoboylab.uz/api/gallery/" + props.match.params.id)
                .then((data) => data.json())
                .then((json) => {
                    changeImg(json.imgPaths)
                    changeTitle(json.title)
                    changeHasData(true)
                })
                .catch(err => console.log(err))
        }
    }, [props.location.imgs, props.location.title, props.match.params.id])
    
    return (
        <div>
            {hasData ? <GalleryView {...props}
                imgs={imgs}
                title={title ? JSON.parse(title)[langForOthers] : comingTitle[langForOthers]} /> : <Loader/>}
        </div>
    )
}

export default GalleryViewWrapper;