import React from 'react';
import classes from './TravelsItem.module.scss';
import { Link } from 'react-router-dom'
const travels = (props) => {
    
    const newTo = {
        pathname: props.link,
        content: props.innerContent,
        title:props.title
    };
    return (
        <Link to={newTo} className={props.main ? null : classes.travelItem}>
            <img src={props.img} alt='travels' />
            <div>
                <h5>
                    {props.title ? props.title : ''}
                    {/* {props.title ?
                        (props.title.length > 35 && !props.main ?
                            props.title.substring(0, 35) + '...' :
                            props.title) :
                        ''
                    } */}
                </h5>
            </div>
        </Link>
    )
}

export default travels