import React, { Component } from 'react';
import { TopPageHeader } from '../../components'
import classes from './Teleshows.module.css'
import background from '../../assets/images/teleshows.jpg'

class Teleshows extends Component {
    state = {
        teleshows: [
            {
                day: "Dushanba",
                date: '23.02.2020',
                items: [
                    {
                        time: '06.00 - 06.30',
                        title: 'Assalom diyor',
                        type: 'Dushanba'
                    },
                ]

            },
            {
                day: "Seshanba",
                date: '23.02.2020',
                items: [
                    {
                        time: '06.00 - 06.30',
                        title: 'Assalom diyor',
                        type: 'Seshanba'
                    },
                    {
                        time: '06.00 - 06.30',
                        title: 'Assalom diyor',
                        type: 'Telenova'
                    },
                ]
            },
            {
                day: "Chorshanba",
                date: '23.02.2020',
                items: [
                    {
                        time: '06.00 - 06.30',
                        title: 'Assalom diyor',
                        type: 'Chorshanba'
                    },
                    {
                        time: '06.00 - 06.30',
                        title: 'Assalom diyor',
                        type: 'Telenova'
                    },
                    {
                        time: '06.00 - 06.30',
                        title: 'Assalom diyor',
                        type: 'Telenova'
                    }
                ]
            },
            {
                day: "Payshanba",
                date: '23.02.2020',
                items: [
                    {
                        time: '06.00 - 06.30',
                        title: 'Assalom diyor',
                        type: 'Payshanba'
                    },
                    {
                        time: '06.00 - 06.30',
                        title: 'Assalom diyor',
                        type: 'Telenova'
                    },
                    {
                        time: '06.00 - 06.30',
                        title: 'Assalom diyor',
                        type: 'Telenova'
                    }
                ]
            },
            {
                day: "Juma",
                date: '23.02.2020',
                items: [
                    {
                        time: '06.00 - 06.30',
                        title: 'Assalom diyor',
                        type: 'Payshanba'
                    },
                    {
                        time: '06.00 - 06.30',
                        title: 'Assalom diyor',
                        type: 'Telenova'
                    },
                    {
                        time: '06.00 - 06.30',
                        title: 'Assalom diyor',
                        type: 'Telenova'
                    }
                ]
            },
            {
                day: "Shanba",
                date: '23.02.2020',
                items: [
                    {
                        time: '06.00 - 06.30',
                        title: 'Assalom diyor',
                        type: 'Payshanba'
                    },
                    {
                        time: '06.00 - 06.30',
                        title: 'Assalom diyor',
                        type: 'Telenova'
                    },
                    {
                        time: '06.00 - 06.30',
                        title: 'Assalom diyor',
                        type: 'Telenova'
                    }
                ]
            },
            {
                day: "Yakshanba",
                date: '23.02.2020',
                items: [
                    {
                        time: '06.00 - 06.30',
                        title: 'Assalom diyor',
                        type: 'Payshanba'
                    },
                    {
                        time: '06.00 - 06.30',
                        title: 'Assalom diyor',
                        type: 'Telenova'
                    },
                    {
                        time: '06.00 - 06.30',
                        title: 'Assalom diyor',
                        type: 'Telenova'
                    }
                ]
            }
        ],
        items: [
            {
                time: '06.00 - 06.30',
                title: 'Assalom diyor',
                type: 'default'
            },
            {
                time: '06.00 - 06.30',
                title: 'Assalom diyor',
                type: 'Telenova'
            },
            {
                time: '06.00 - 06.30',
                title: 'Assalom diyor',
                type: 'Telenova'
            }
        ]
    }

    contentHandler = () => {
        return (
            this.state.items.map((entry, index) =>
                <div key={index} className={classes.item}>
                    <div>
                        {entry.time}
                    </div>
                    <div>
                        <p>{entry.title}</p>
                        <span>{entry.type}</span>
                    </div>

                </div>
            )
        )
    }

    render() {

        const buttons = this.state.teleshows.map(
            (entry, index) => {
                return (
                    <div key={index} className={classes.button}
                        onClick={() => this.setState({ items: entry.items })}>
                        <button
                            type="button"
                            className="btn btn-link">
                            {entry.day}
                        </button>
                        <span>{entry.date}</span>
                    </div>
                )
            }
        )


        return (
            <>
                <TopPageHeader title="Tele-dasturlar" background={background} />
                <section className={classes.teleshows}>
                    <div className={classes.wrapper}>
                        <div className="container">
                            <div className={classes.buttons}>
                                {buttons}
                            </div>
                        </div>
                    </div>
                    <div className={classes.items}>
                        <div className="container">
                            {this.contentHandler()}
                        </div>
                    </div>
                </section>
            </>
        );
    }

};


export default Teleshows;