import React, { Component } from 'react';
import logo from '../../assets/images/LogoLoader.svg'
class WithData extends Component {

	state = {
		data: [],
		received: false
	}

	componentDidMount() {
		if (!this.props.flag) {
			fetch(this.props.url)
				.then(response => response.json())
				.then(json => {
					this.setState({ received: true, data: json })
					this.props.setData(json)
				})

		} else {
			this.setState({ received: true, data: this.props.appState })
		}
	}
	render() {
		// console.log(this.state.data);

		const C = this.props.C;
		return (
			<div>
				{this.state.received ? <C data={this.state.data} {...this.props}></C> :
					<div className="loader">
						<img src={logo} alt="logo" />
					</div>
				}
			</div >
		);
	}
};
export default WithData;