import React, { Component } from 'react';
import uuidv4 from "uuid";
import Carousel from "react-spring-3d-carousel"
import { config } from "react-spring";
import i from "../../assets/images/travel_1.png"
class Slider3d extends Component {
	state = {
    goToSlide: 0,
    offsetRadius: 2,
    showNavigation: true,
    config: config.gentle
  };
	slides = [
		  {
		    key: uuidv4(),
		    content: <img src={i} alt="1" width="450px" height="250px" style={{objectFit:"cover"}}/>
		  },
		  {
		    key: uuidv4(),
		    content: <img src={i} alt="2" width="450px" height="250px" style={{objectFit:"cover"}}/>
		  },
		  {
		    key: uuidv4(),
		    content: <img src={i} alt="3" width="450px" height="250px" style={{objectFit:"cover"}}/>
		  },
		  {
		    key: uuidv4(),
		    content: <img src={i} alt="4" width="450px" height="250px" style={{objectFit:"cover"}}/>
		  },
		  {
		    key: uuidv4(),
		    content: <img src={i} alt="5" width="450px" height="250px" style={{objectFit:"cover"}}/>
		  },
		  {
		    key: uuidv4(),
		    content: <img src={i} alt="6" width="450px" height="250px" style={{objectFit:"cover"}}/>
		  },
		  {
		    key: uuidv4(),
		    content: <img src={i} alt="7" width="450px" height="250px" style={{objectFit:"cover"}}/>
		  },
		  {
		    key: uuidv4(),
		    content: <img src={i} alt="8" width="450px" height="250px" style={{objectFit:"cover"}}/>
		  }
		].map((slide, index) => {
    return { ...slide, onClick: () => this.setState({ goToSlide: index }) };
  });

	render() {
		return (
			<div style={{ backgroundColor:"#222831", padding:"50px 0"}}>
				<div className="container">
					<div style={{position:"relative",height:"400px",}}>
					<Carousel slides={this.slides} goToSlide={this.state.goToSlide}/>
		    		</div>
		    	</div>
		    </div>
		);
	}
}

export default Slider3d