import React from "react";
import slider1 from '../../assets/images/slider1.png'
import Slider from '../UI/Slider/Slider';

const mainSlider = (props) => {

    const ncont = props.data.premiere.data.map(entry => {
        return {
            title: JSON.parse(entry.content)[props.lang],
            description: JSON.parse(entry.title)[props.lang],
            day: JSON.parse(entry.content_html)[props.lang],
            time: JSON.parse(entry.short_content)[props.lang],
            image: entry.imgPaths ? (entry.imgPaths.length > 0 ? entry.imgPaths[0] : slider1) : slider1,
            // user: "Luan Gjokaj",
            // userProfile: "https://i.imgur.com/JSW6mEk.png"
        }
    })

    // autoplay={2000}
    return (
        <Slider content={ncont} />
    );
}

export default mainSlider;
