import React from 'react';
import { WithData } from "../../HOCs"
import Dasturlar from "./Dasturlar"
import AppContext from "../../Context/AppContext"
import { baseUrl } from "../../Constants"
const DasturlarWrapper = (props) => {
	return (
		<AppContext.Consumer>
			{(context) => {
				return <WithData C={Dasturlar}
					url={baseUrl + "/api/pros"}
					setData={context.setDasturlar}
					flag={context.dasturlarGot}
					appState={context.dasturlar}
					{...props}
				/>
				}
			}
		</AppContext.Consumer>
	)
}

export default DasturlarWrapper;