import React from 'react';
import IndexBuilder from "./IndexBuilder"
import { FetchMultiple } from "../../HOCs"
import { baseUrl } from "../../Constants"
import AppContext from "../../Context/AppContext"

const IndexWrapper = (props) => {
	const apiList = [
		{
			url: baseUrl + "/api/premiere?main=true",
			for: "premiere"
		},
		{
			url: baseUrl + "/api/news?main=true",
			for: "news"
		},
		{
			url: baseUrl + "/api/pros?main=true",
			for: "programs"
		},
		{
			url: baseUrl + "/api/trips?main=true",
			for: "trips"
		},
		{
			url: baseUrl + "/api/gallery?main=true",
			for: "gallery"
		}
	]

	return (
		<AppContext.Consumer>
			{(context) => {
				return <FetchMultiple C={IndexBuilder}{...props}
					urls={apiList}
					setData={context.setIndex}
					appState={context.index}
					flag={context.indexGot}
					{...props}
				/>
			}
			}
		</AppContext.Consumer>
		// <FetchMultiple C={IndexBuilder} urls={apiList} {...props} />
	)
}

export default IndexWrapper;