import React, { useEffect, useState } from 'react';
import NewsItem from './NewsItem/NewsItem'
import SectionTitle from '../../components/UI/SectionTitle/SectionTitle';
import classes from './News.module.scss';
import Carousel from "nuka-carousel"
import "./News.css"
import { useMediaQuery } from 'react-responsive'
import { NavLink } from "react-router-dom"
import { lang } from '../../Helpers/Helpers'

const News = (props) => {

    const list = props.data.map((entry) => (
        <NewsItem date={entry.inserted_at} lang={props.lang} id={entry.id} src={entry.imgPaths[0]} content={JSON.parse(entry.content)[props.lang]} key={entry.id} title={JSON.parse(entry.title)[props.lang]} />
    ));

    const [slidesCount, changeCount] = useState(4)
    const isTablet = useMediaQuery({ maxDeviceWidth: 992 })
    const isPhone = useMediaQuery({ maxDeviceWidth: 576 })

    const calc = () => {
        if (isTablet || isPhone) {
            changeCount(isPhone ? 1 : 2)
        } else {
            changeCount(4)
        }
    }
    useEffect(calc, [isTablet, isPhone])

    return (
        <section className={classes.news}>
            <div className="container">
                <div className={classes.newsTop}>
                    <SectionTitle content={lang(props.lang, 'News')} />
                    <NavLink to={'/' + props.lang + '/allNews'} className={classes.newsNavigator} >{lang(props.lang, 'Allnews')}</NavLink>
                </div>
            </div>
            <div className="container">
                <div className={classes.mainNews + " newsCarousel"}>
                    <Carousel 
                    wrapAround={true} 
                    slidesToShow={slidesCount} 
                    dragging={slidesCount > 3 ? false : true} 
                    autoplay={slidesCount > 3 ? false : false} 
                    cellSpacing={20} 
                    framePadding="0 0 50px"
                    width="95%">
                        {list}
                    </Carousel>
                </div>
            </div>
        </section>
    );
}

export { News }