import React, { Component } from 'react';
import { PContent, ProgramHeader } from "../../components"
import "./Dastur.scss"

class Dastur extends Component {
	componentDidMount(){
		window.scrollTo(0, 0);
	}
	render() {
		console.log(this.props.data);
		return (
			<div className="programsInside">
				<ProgramHeader
					lang={this.props.match.params.lang}
					title={this.props.location.title ? this.props.location.title : JSON.parse(this.props.data.title)[this.props.match.params.lang]}
					day={this.props.location.day ? this.props.location.day : JSON.parse(this.props.data.content_html)[this.props.match.params.lang]}
					time={this.props.location.time ? this.props.location.time : JSON.parse(this.props.data.short_content)[this.props.match.params.lang]}
					img={this.props.location.imgs ? this.props.location.imgs : this.props.data.imgPaths}
					videoLink={this.props.location.videoLink ? this.props.location.videoLink : this.props.data.options} />

				<PContent
					content={this.props.location.content ? this.props.location.content : JSON.parse(this.props.data.content)[this.props.match.params.lang]}
					title={this.props.location.title ? this.props.location.title : JSON.parse(this.props.data.title)[this.props.match.params.lang]} />
				{/* <Slider3d /> */}
			</div>
		)
	}
}

export default Dastur;