import React from 'react';
import classes from "./ProgramHeader.module.css"
import PageContainer from "../UI/PageContainer/PageContainer"
import i from "../../assets/images/programs1.png"
import { Time } from "../"
import Alt from '../../HOCs/AltForImgs/AltForImgs'
const alt = <Alt src={i} />
const ProgramHeader = (props) => {
	const watch = {
		uz: "",
		oz: "Ko'rish",
		ru: "",
		en: "Watch",
	}

	return (
		<div className={classes.root}>
			<PageContainer>
				<div className={`row ${classes.myRow}`}>
					<div className="col-md-6">
						<div className={classes.imgWrap}>
							<img src={props.img[0]} alt={alt} />
						</div>
					</div>
					<div className="col-md-6">
						<div className={classes.pinfo}>
							<h1 className={classes.name}>
								{props.title}
							</h1>
							<Time data={{ day: props.day, time: props.time }} style={{ color: "white", fontSize: "25px" }} />
							<div className={classes.linkWrap}>
								<a href={props.videoLink} target="_blank" rel="noopener noreferrer">
									{watch[props.lang]}
								</a>
							</div>
						</div>

					</div>
				</div>
			</PageContainer>
		</div>
	)
}

export default ProgramHeader;