import React, { useState, useEffect } from 'react';
import InnerContent from '../../components/InnerContent/InnerContent'
import AppContext from "../../Context/AppContext"
import logo from '../../assets/images/LogoLoader.svg'

const ProgramsContent = (props) => {
    var [hasData, changeHasData] = useState(false);
    var [content, changeContent] = useState("");
    var [title, changeTitle] = useState("");

    useEffect(() => {
        if (props.location.content && props.location.title) {
            changeHasData(true)
        } else {
            console.log('hi');
            fetch("http://admin.dunyoboylab.uz/api/trips/" + props.match.params.id)
                .then((data) => data.json())
                .then((json) => {
                    changeContent(JSON.parse(json.content)[props.match.params.lang])
                    changeTitle(JSON.parse(json.title)[props.match.params.lang])
                    changeHasData(true)
                })
                .catch(err => console.log(err))
        }
    }, [props.location.content, props.location.title, props.match.params.lang, props.match.params.id])

    return (


        <div>
            {hasData ?
                <AppContext.Consumer>
                    {(context) => {
                        return <InnerContent
                            content={props.location.content ? props.location.content : content}
                            title={props.location.title ? props.location.title : title}
                            recentData={context.index.sayohatlar}
                            id={props.match.params.id}
                            category='trips' />
                    }}
                </AppContext.Consumer> :
                <div className="loader">
                    <img src={logo} alt="logo" />
                </div>
            }
        </div>
        // <div>
        //     {hasData ?
        //         <InnerContent
        //             slider
        //             content={props.location.content ? props.location.content : content}
        //             title={props.location.title ? props.location.title : title} /> :
        //         "spinning"}
        // </div>
    )
}

export default ProgramsContent;
