import React, { useState, useEffect } from 'react';
import SocialItem from './SocialItem.js/SocialItem';
import { baseUrl } from '../../../Constants/'
// import { socialItems } from '../../../HOCs/FetchSettings/FetchIndex';
// import { Footer } from '../../'
import classes from './SocialItems.module.scss'
import { faInstagram, faTelegramPlane, faFacebookF, faYoutube } from '@fortawesome/free-brands-svg-icons'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
// import socialItem from './SocialItem.js/SocialItem';
// import IndexContext from '../../../Context/IndexContext';

const telegram = <FontAwesomeIcon color="#fff" icon={faTelegramPlane} />
const instagram = <FontAwesomeIcon color="#fff" icon={faInstagram} />
const facebook = <FontAwesomeIcon color="#fff" icon={faFacebookF} />
const youtube = <FontAwesomeIcon color="#fff" icon={faYoutube} />

const SocialItems = (props) => {
    // console.log(socialItems);

    const [socials, setCount] = useState([]);

    useEffect(() => {
        fetch(`${baseUrl}/api/settings`)
            .then((response) => {
                return response.json()
            })
            .then((response) => {
                setCount(response)
            })
            .catch((error) => {
                console.log(error);
            })

    }, [])

    // console.log(socials);
    const sorted = socials.sort(function (a, b) {
        return a.id - b.id;
    });
    // console.log(b);

    const list = sorted.slice(0, 4).map((item, index) => {
        return (
            <SocialItem link={JSON.parse(item.short_content)['ru']} img={eval(JSON.parse(item.title)['ru'])} key={index} />
        )
    })

    // const footer = () => sorted.slice(4)

    return (
        < ul className={classes.socialItems} >
            {list}
        </ul >
    )
}
// console.log(footer);

export default SocialItems