import React from 'react';
import classes from './PhotoGallery.module.scss'
import Pagination from '../../HOCs/Pagination/Pagination';
import { lang } from '../../Helpers/Helpers'
import TopPageHeader from '../TopPageHeader/TopPageHeader';
import background from '../../assets/images/gallery.png'
const photoGallery = (props) => {

    return (
        <>
            <TopPageHeader title={lang(props.lang, 'gallery')} background={background} />
            <section className={classes.photoGallery}>
                <div className="container">
                    <div className="row">
                        <Pagination
                            data={props.data.data}
                            lang={props.lang}
                            item={'gallery'}
                            
                        />
                    </div>
                </div>
            </section>
        </>
    )
};

export default photoGallery;