import React, { Component } from 'react';
import classes from "./Sayohatlar.module.css"
import { TopPageHeader, ListBlock } from "../../components"
import background from '../../assets/images/travels-2.jpg'


class Sayohatlar extends Component {
	componentDidMount() {
		window.scrollTo(0, 0);
	}

	render() {
		return (<div className={classes.root}>
			<TopPageHeader title="Sayohatlar" background={background} />
			<div className="container">
				<div className={classes.padded}>
					<ListBlock
						page="sayohatlar"
						data={this.props.data.data}
						lang={this.props.match.params.lang}
						toPage="sayohat" />
				</div>
			</div>
		</div>
		);
	}
}
export default Sayohatlar