import React from 'react';
import NavLink from './NavLink/NavLink';
import classes from './NavLinks.module.scss';
import Logo from '../UI/Logo/Logo';
import {lang} from '../../Helpers/Helpers'
// import { navigation } from "../../Data"

const navLinks = (props) => {

    const navigation = [
        {
            title: lang(props.lang, 'shows'),
            link: 'programs'
        },
        {
            title: lang(props.lang, 'weekly'),
            link: "teleshow"
        },
        {
            title: lang(props.lang, 'News'),
            link: 'allNews'
        },
        {
            title: lang(props.lang, 'world'),
            link: 'sayohatlar'
        },

        {
            title: lang(props.lang, 'gallery'),
            link: 'gallery'
        },
        {
            title: lang(props.lang, 'contact'),
            link: 'contact'
        }
    ];
    const linksList = navigation.map((entry, index) => (
        <NavLink link={props.lang + "/" + entry.link} key={index} closeSideBar={props.close}>{entry.title}</NavLink>
    ));

    let attachedClasses = [classes.SideDrawer, classes.Close];
    if (props.open) {
        attachedClasses = [classes.SideDrawer, classes.Open];
    }
    return (
        <div className={props.header ? attachedClasses.join(' ') : classes.footer} >
            {props.logo ? <div className={classes.logo}>
                <Logo closeSideBar={props.close} lang={props.lang} footer />
            </div> : null}
            <ul>
                {linksList}
            </ul>
        </div>
    )
};

export default navLinks;

