import React, { Component } from 'react'
import classes from './InnerContent.module.scss'
import FullContent from '../../HOCs/FullCont/FullCont'
import noImg from '../../assets/images/noimg.png';
// import Adverts from '../Adverts/Adverts'
import { baseUrl } from '../../Constants'
import { langForOthers, lang } from '../../Helpers/Helpers'
import { Link } from 'react-router-dom'
import { Helmet } from 'react-helmet'
// import { Title, Amazings } from ".."

class InnerContent extends Component {
    constructor(props) {
        super(props);
        this.state = {
            recentData: this.props.recentData ? this.props.recentData : [],
            recentDataHtml: '',
            category: this.props.category
        }
    }

    componentDidMount() {
        if (!this.props.recentData) {
            fetch(baseUrl + `/api/${this.state.category}?main=true`)
                .then(response => response.json())
                .then(json => {
                    this.setState({
                        recentData: json
                    })
                })
                .catch(err => console.log(err))
        }
        window.scrollTo(0, 0)
    }

    render() {

        let recentData = '';
        if (this.state.recentData.length > 0) {
            recentData = this.state.recentData
                .filter(item => {
                    return (item.id !== parseInt(this.props.id))
                })
                .map((item) => {
                    return (
                        <Link to={`/${langForOthers}/${this.state.category === 'news' ? 'newsContent' : 'sayohatlar'}/${item.id}`} className={classes.newsCard} key={item.id}>
                            <img src={item.imgPaths[0] ? item.imgPaths[0] : noImg} alt="" />
                            <p dangerouslySetInnerHTML={{
                                __html: JSON.parse(item.title)[langForOthers]
                            }} />
                        </Link>
                    )
                })
        }

        return (
            <>
                <Helmet>
                    <title>{this.props.title ? this.props.title.slice(0, 60) : 'notitle'}</title>
                    <meta name="description" content={this.props.content ? this.props.content.slice(0, 200).replace(/<\/?[^>]+>/ig, " ") : 'noDescription'} />
                </Helmet>
                <section className={classes.news}>
                    <FullContent>
                        <div className={'col-lg-9 '}>
                            <h1>{this.props.title}</h1>
                            <div className={classes.newsContent} dangerouslySetInnerHTML={{
                                __html: this.props.content
                            }}></div>
                        </div>
                        <div className='col-lg-3'>
                            <div className={classes.recentNews}>
                                <h5>{this.state.category === 'news' ?
                                    lang(langForOthers, 'recent-news') :
                                    lang(langForOthers, 'recent-travels')}</h5>
                                {recentData}
                            </div>
                        </div>
                    </FullContent>
                    {/* <Adverts /> */}
                </section>
                {/* <section className={classes.manyRead}>
                    <div className="container">
                        <Title content="Qiziqarli" />
                        <Amazings />
                    </div>
                </section> */}
            </>
        )
    }
}

export default InnerContent;