import React from 'react';
import { WithData } from "../../HOCs"
import Sayohatlar from "./Sayohatlar"
import AppContext from "../../Context/AppContext"
import { baseUrl } from "../../Constants"

const SayohatlarWrapper = (props) => {
	return (
		<AppContext.Consumer>
			{(context) => {
				return <WithData C={Sayohatlar}
					url={baseUrl + "/api/trips"}
					setData={context.setSayohatlar}
					flag={context.sayohatlarGot}
					appState={context.sayohatlar}
					{...props}
				/>
			}
			}
		</AppContext.Consumer>
	)
}

export default SayohatlarWrapper;