import React from 'react';
import classes from './Footer.module.scss';
import './Footer.css';
import Logo from '../UI/Logo/Logo';
import NavLinks from '../NavLinks/NavLinks'
import SocialItems from '../UI/SocialItems/SocialItems'
import { NavLink } from "react-router-dom"
import { faPhone, faEnvelope, faMapMarkerAlt } from '@fortawesome/free-solid-svg-icons'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { lang } from '../../Helpers/Helpers'
const phone = <FontAwesomeIcon color="#fff" icon={faPhone} />
const mail = <FontAwesomeIcon color="#fff" icon={faEnvelope} />
const location = <FontAwesomeIcon color="#fff" icon={faMapMarkerAlt} />

const footer = (props) => {
    // console.log(props.match.params.lang);

    return (
        <footer className={classes.footer}>
            <div className="container">
                <div className={classes.footerMain}>
                    <div className={classes.logo}>
                        <Logo lang={props.match.params.lang} footer />
                        <NavLink to='/'><h2>{lang(props.match.params.lang, 'bio')}</h2></NavLink>
                    </div>
                    <div className={classes.footerMiddle}>
                        <NavLinks lang={props.match.params.lang} />
                    </div>
                    <div className={classes.footerRight}>
                        <ul>
                            <li>
                                <p>
                                    <span className="iconic">{location}</span>
                                    {lang(props.match.params.lang, 'address')}</p>
                            </li>
                            <li>
                                <a href='tel:+998712149929'>
                                    <span className="iconic">{phone}</span><span>+998 71 214-99-29</span>
                                </a>
                            </li>
                            <li>
                                <a href='mailto:info@mtrk.uz'>
                                    <span className="iconic">{mail}</span>
                                    <span>info@mtrk.uz</span>
                                </a>
                            </li>
                            <li className={classes.socialFooter}>
                                <SocialItems />
                            </li>
                        </ul>
                    </div>
                </div>
                <div className={classes.footerBottom}>
                    <p> {(new Date().getFullYear()) + ' ' + lang(props.match.params.lang, 'rights')}</p>
                </div>
            </div>

        </footer>
    )
}

export default footer