import React from 'react';
import classes from './Programs.module.scss';
import './Programs.scss'
import SectionTitle from '../UI/SectionTitle/SectionTitle';
import Slider from '../UI/Slider/Slider';
import {lang} from '../../Helpers/Helpers'
const programs = (props) => {
    const content = props.data.map(item => {
        
        return ({
            title: JSON.parse(item.title)[props.lang],
            content: JSON.parse(item.content)[props.lang],
            day: JSON.parse(item.content_html)[props.lang],
            time: JSON.parse(item.short_content)[props.lang],
            imgs: item.imgPaths ? [...item.imgPaths] : [],
            link: '/' + [props.lang] + '/programs/' + JSON.parse(item.id),
            videoLink: item.options
        })
    })
    
    return (
        <section className={classes.programs}>
            <div className="container">
                <SectionTitle content={lang(props.lang, 'programs')} color={'white'} />
                <div className="programs">
                    <Slider content={content} styles={programs} />
                </div>
            </div>
        </section>
    )
}
export default programs