import React, { Component } from 'react';
import ReactPaginate from 'react-paginate';
import { faChevronRight, faChevronLeft } from '@fortawesome/free-solid-svg-icons'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { GalleryItem } from "../../components/"
import AppContext from '../../Context/AppContext'
import classes from './pagination.module.css'
let uniqid = require('uniqid');
class ListBlock extends Component {
    constructor(props) {
        super(props);
        this.state = {
            offset: 0,
            data: [],
            perPage: 8,
            currentPage: 0,
            pageCount: null,
            lang: this.props.lang
        };
    }
    receivedData() {
        const data = this.props.data
        const slice = data.slice(this.state.offset, this.state.offset + this.state.perPage)
        const list = slice.map((entry) => {
            return (
                <AppContext.Consumer key={uniqid()}>
                    {(context) => {
                        return (<GalleryItem
                            img={entry.imgPaths}
                            lang={this.props.lang}
                            date={entry.inserted_at}
                            key={uniqid()}
                            id={entry.id}
                            likes={entry.views}
                            title={JSON.parse(entry.title)[this.props.lang]}
                            setLikes={context.setLikes}
                            appState={context.likes}
                            flag={context.likesGot}
                        />)
                    }}
                </AppContext.Consumer>
            )


            // return (
            //     <GalleryItem lang={this.props.lang} img={entry.imgPaths} title={JSON.parse(entry.title)[this.props.lang]} date={entry.inserted_at} key={entry.id} id={entry.id} likes={entry.views}/>
            // )
            // switch (this.props.item) {
            //     case 'gallery':
            //         return (
            //             <GalleryItem lang={this.props.lang} img={entry.imgPaths} title={JSON.parse(entry.title)[this.props.lang]} date={entry.inserted_at} key={entry.id} id={entry.id} />
            //         )
            //     default:
            //         break;
            // }

        });
        this.setState({
            pageCount: Math.ceil(data.length / this.state.perPage),
            list
        })
    }

    handlePageClick = (e) => {
        const selectedPage = e.selected;
        const offset = selectedPage * this.state.perPage;
        this.setState({
            currentPage: selectedPage,
            offset: offset
        }, () => {
            this.receivedData()
        });

    };
    langHandler = () => {
        if (this.props.lang !== this.state.lang) {
            this.componentDidMount()
            this.setState({ lang: this.props.lang })
        }
    }

    componentDidMount() {
        this.receivedData()
    }

    componentDidUpdate() {
        this.langHandler()
    }
    render() {
        return (
            <>
                {this.state.list}
                {this.state.pageCount > 1 ?
                    <ReactPaginate
                        previousLabel={<FontAwesomeIcon color="#000" icon={faChevronLeft} />}
                        nextLabel={<FontAwesomeIcon color="#000" icon={faChevronRight} />}
                        breakLabel={"..."}
                        breakClassName={"break-me"}
                        pageCount={this.state.pageCount}
                        marginPagesDisplayed={1}
                        pageRangeDisplayed={2}
                        onPageChange={this.handlePageClick}
                        containerClassName={`pagination ${classes.pagination}`}
                        subContainerClassName={"pages pagination"}
                        activeClassName={"active"} /> : null
                }
            </>
        )
    }

}

export default ListBlock;