import React, { useState, useEffect } from 'react';
import NewsContent from "./NewsContent"
import logo from '../../../assets/images/LogoLoader.svg'
const NewsContentWrapper = (props) => {
  var [hasData, changeHasData] = useState(false);
  var [content, changeContent] = useState("");
  var [title, changeTitle] = useState("");

  useEffect(() => {
    if (props.location.content && props.location.title) {
      changeHasData(true)
    } else {
      fetch("http://admin.dunyoboylab.uz/api/news/" + props.match.params.id)
        .then((data) => data.json())
        .then((json) => {
          changeContent(JSON.parse(json.content)[props.match.params.lang])
          changeTitle(JSON.parse(json.title)[props.match.params.lang])
          changeHasData(true)
        })
        .catch(err => console.log(err))
    }
  }, [changeHasData, props.location.content, props.location.title, props.match.params.lang, props.match.params.id, changeContent])

  return (
    <div>
      {hasData ? <NewsContent {...props} content={content} title={title} id={props.match.params.id} /> :
        <div className="loader">
          <img src={logo} alt="logo" />
        </div>
      }
    </div>
  )
}

export default NewsContentWrapper;