import React from 'react';
import classes from './Gallery.module.scss';
import GalleryItem from './GalleryItem/GalleryItem'
import {lang} from '../../Helpers/Helpers'
// import gallery_3 from '../../assets/images/gallery_3.png'
const gallery = (props) => {

    const galleryList = props.data.map((entry, index) => {
        // const pathname = '/' + props.lang + "/gallery/" + entry.id
        return (
            <GalleryItem 
            // img={(entry.imgPaths) ? (entry.imgPaths.length > 0 ? entry.imgPaths[0] : gallery_3) : gallery_3} 
            img={entry.imgPaths} 
            title={JSON.parse(entry.title)} 
            key={index} 
            id={entry.id} 
            lang={props.lang}/>
        )
    });

    return (
        <section className={classes.gallery}>
            <div className="container">
                <h2>{lang(props.lang, 'gallery')}</h2>
                <div className={classes.galleryList + ' gallery-list'}>
                    <div className="row">
                        {galleryList}
                    </div>
                </div>
            </div>
        </section>

    )
}

export default gallery