const oz = (content) => {
    switch (content) {
        case 'News':
            return 'Yangiliklar'
        case 'Allnews':
            return 'Barcha Yangiliklar'
        case 'programs':
            return 'Dasturlar'
        case 'travels':
            return "Dunyo Bo'ylap Sayohat"
        case 'gallery':
            return "Galereya"
        case 'gallery-desc':
            return "Dunyo Bo'ylap galereyasi"
        case 'bio':
            return "Siz bilan dunyo bo'ylab birga!"
        case 'address':
            return "Toshkent shahri, 100011, A.Navoi ko`chasi 69"
        case 'rights':
            return "Dunyo Bo'ylab TV kanali Barcha huquqlar himoyalangan. Tasdiqlanmagan holda materiallardan nusxa olish taqiqlanadi."
        case 'shows':
            return "Ko'rstuvlar"
        case 'weekly':
            return "Haftalik teledasturlar"
        case 'world':
            return "Dunyo Bo'ylab"
        case 'contact':
            return "Aloqa"
        case 'read-more':
            return "Batafsil"
        case 'index-title':
            return "Dunyo Bo'ylab | Rasmiy telekanal"
        case 'index-desc':
            return "O'zbekiston milliy teleradiokompaniyasi, Dunyo bo'ylab telekanali rasmiy veb-sayti"
        case 'recent-news':
            return "Eng so'ngi yangiliklar"
        case 'recent-travels':
            return "Eng so'ngi sayohatlar"
        case 'allnews-desc':
            return "Dunyo Bo'ylap yangiliklar"
        default:
            break;
    }
}

export { oz }