import React from "react";
import PhotoGallery from '../../components/PhotoGallery/PhotoGallery'
// import Adverts from '../../components/Adverts/Adverts';
import { lang } from '../../Helpers/Helpers'
import { Helmet } from 'react-helmet'
const galleryBuilder = props => {
    // console.log(props.data);
    
    return (
        <>
            <Helmet>
                <title>{lang(props.match.params.lang, 'gallery')}</title>
                <meta name="description" content={lang(props.match.params.lang, 'gallery-desc')} />
            </Helmet>
            <PhotoGallery lang={props.match.params.lang} data={props.data} />
            {/* <Adverts /> */}
        </>
    )
}

export default galleryBuilder;

